p, main, nav, section{ display: block }\n\nhtml{\n    color: #353535;\n    scroll-behavior: smooth;\n    font: 16px/1.5 'Microsoft Yahei', 'PingFang SC', 'Hiragino Sans GB', sans-serif;\n}\n\nhtml.font-s{ font-size: 14px }\nhtml.font-m{ font-size: 16px }\nhtml.font-l{ font-size: 18px }\n\n:root{\n    --red: #ea644a;\n    --yellow: #ffb03a;\n    --blue: #3498db;\n    --green: #27a17e;\n}\n\n@media screen and (max-width: 768px){\n    html.font-auto{ font-size: 14px }\n}\n\n@media screen and (min-width: 1921px){\n    html.font-auto{ font-size: 18px }\n}\n\n/* - 选择内容 */\n::-moz-selection{\n    color: #fff;\n    background-color: rgba(0, 0, 0, .66);\n}\n\n::selection{\n    color: #fff;\n    background-color: rgba(0, 0, 0, .66);\n}\n\n/* - 滚动条 */\n::-webkit-scrollbar{\n    width: 10px;\n    height: 10px;\n}\n\n::-webkit-scrollbar-thumb{\n    background: #ccc;\n    border-radius: 5px;\n}\n::-webkit-scrollbar-track{ border-radius: 5px }\n::-webkit-scrollbar-track:hover{ background: rgba(0, 0, 0, .05) }\n\nbody::-webkit-scrollbar-track{ border-radius: 0 }\n\n/* 1 - 容器\n-------------------------------- */\n.wrap{\n    margin: 0 auto;\n    max-width: 1200px;\n    padding: 0 1.25em;\n    box-sizing: content-box;\n}\n\n.wrap.min{ max-width: 800px }\n.wrap.mid{ max-width: 1000px }\n.wrap.max{ max-width: 1400px }\n.wrap.full{ max-width: 100% }\n\n.wrap.thin{ padding: 0 .75em }\n.wrap.thick{ padding: 0 1.5em }\n.wrap.clear{\n    padding-left: 0;\n    padding-right: 0;\n}\n\n.float-none{ float: none !important }\n.float-left{ float: left !important }\n.float-right{ float: right !important }\n\n.clearfix:after{\n    content: '';\n    clear: both;\n    display: block;\n}\n\n/* 2 - 元素\n-------------------------------- */\nh1{ font-size: 2em }\n\nh1, h2, h3, h4, h5, h6{ margin-bottom: 1rem }\n\nh1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child, p:last-child{ margin-bottom: 0 }\n\np{\n    line-height: 1.8;\n    margin-bottom: 1em;\n}\n\na{\n    color: #3498db;\n    text-decoration: none;\n}\n\na:hover{ color: #ffc670 }\n\nabbr[title]{\n    cursor: help;\n    text-decoration: none;\n    border-bottom: 1px dotted;\n}\n\nem, mark, kbd{\n    font-size: .85em;\n    padding: .25em .5em;\n    border-radius: .5em;\n}\n\nem{\n    color: #fff;\n    font-style: normal;\n    background: #3498db;\n}\nem.red{ background: var(--red) }\nem.yellow{ background: var(--yellow) }\nem.blue{ background: var(--blue) }\nem.green{ background: var(--green) }\n\nkbd{\n    color: #fff;\n    background: #333;\n    font-family: 'Consolas', 'Courier New', monospace, \"微软雅黑\";\n}\n\nimg, svg, audio, video, iframe{\n    max-width: 100%;\n    vertical-align: middle;\n}\n\naudio, video{ outline: none }\n\n/* - 文章 */\narticle{\n    word-break: break-all;\n    letter-spacing: .03em;\n}\n\narticle > *{ margin-bottom: 1em }\narticle > *:last-child{ margin-bottom: 0 }\n\narticle h1, article h2, article h3{ font-size: 1.2em }\narticle h4, article h5, article h6{ font-size: 1.1em }\n\narticle ul, article ol, article dl{ line-height: 1.8 }\n\n/* - 按钮 */\nbutton{\n    outline: 0;\n    font: inherit;\n}\n\n.btn{\n    color: inherit;\n    cursor: pointer;\n    background: #fff;\n    padding: .5em 1em;\n    border-radius: 4px;\n    display: inline-block;\n    border: 1px solid transparent;\n}\n.btn:hover{ color: inherit }\n\n/* -- 禁用的按钮 */\n.btn[disabled]{\n    opacity: .5;\n    cursor: not-allowed;\n}\n\n/* -- 按钮尺寸 */\n.btn.small{ font-size: .85em }\n.btn.middle, .btn.large{ padding: .75em 1.5em }\n.btn.large{ font-size: 1.2em }\n\n/* -- 按钮颜色 */\n.btn.red, .btn.yellow, .btn.blue, .btn.green{ color: #fff }\n\n.btn.red{\n    background: #ea644a;\n    background: var(--red);\n}\n.btn.yellow{\n    background: #ffb03a;\n    background: var(--yellow);\n}\n.btn.blue{\n    background: #3498db;\n    background: var(--blue);\n}\n.btn.green{\n    background: #27a17e;\n    background: var(--green);\n}\n.btn.transparent{ background: transparent }\n\n/* - 代码 */\npre, code{ font-family: 'Consolas', 'Courier New', monospace, \"微软雅黑\" }\n\n:not(pre) > code{\n    color: #c40b00;\n    font-size: 85%;\n    word-wrap: normal;\n    border-radius: .5em;\n    padding: .25em .5em;\n    word-break: break-all;\n    background-color: #f7f2f4;\n}\n\npre > code{\n    color: #fff;\n    tab-size: 4;\n    padding: 1em;\n    display: block;\n    overflow-x: auto;\n    word-break: normal;\n    font-size: inherit;\n    border-radius: 5px;\n    background-color: #333;\n}\n\n/* - 项目列表 */\nul, ol{ margin-left: 1.25em }\nul.clear, ol.clear{\n    margin-left: 0;\n    list-style: none;\n}\n\ndl dd{ margin-left: 1.5em }\ndl dd:before{\n    content: \"--\";\n    margin-right: .25em;\n}\n\n/* - 补间动画 */\na, .btn{\n    transition: color 0.3s, background 0.3s;\n    -o-transition: color 0.3s, background 0.3s;\n    -moz-transition: color 0.3s, background 0.3s;\n    -webkit-transition: color 0.3s, background 0.3s;\n}\n\n/* - 引用*/\nblockquote{\n    margin: 0 0 1em;\n    line-height: 1.8;\n    font-style: oblique;\n    background: #f5fafd;\n    padding: 1em 1em 1em 2em;\n    border-left: 5px #3498db solid;\n}\n\ncite{\n    color: #3498db;\n    font-style: normal;\n}\n\n/* - 分割线 */\nhr{\n    border: 0;\n    margin: 1.5em 0;\n    border-top: 1px rgba(0, 0, 0, 0.5) solid;\n}\nhr.light{\n    border-top: 1px rgba(255, 255, 255, 0.5) solid;\n}\n\n/* - 表单 */\ninput[disabled], textarea[disabled]{\n    cursor: no-drop !important;\n}\n\ninput[disabled], textarea[disabled], input[readonly]{\n    background: rgb(235, 235, 228);\n}\n\ninput, select, textarea{\n    outline: none;\n    font: inherit;\n    max-width: 100%;\n    background: none;\n    vertical-align: middle;\n}\n\ninput[type*=\"date\"], input[type=\"email\"], input[type=\"month\"], input[type=\"number\"], input[type=\"password\"], input[type=\"search\"], input[type=\"tel\"], input[type=\"text\"], input[type=\"time\"], input[type=\"url\"], input[type=\"week\"],\nselect, textarea{\n    padding: .5em;\n    color: inherit;\n    border-radius: 4px;\n    border: #ccc 1px solid;\n    min-height: calc(2.5em + 2px);\n}\n\ninput.invalid, input:out-of-range{\n    border-color: #c40b00;\n    background: rgba(255, 0, 0, .1);\n}\n\n::-webkit-file-upload-button{\n    color: #fff;\n    border: none;\n    outline: none;\n    display: block;\n    padding: .5em 1em;\n    background: #3498db;\n    border-radius: .5em;\n}\n\ninput[type=\"range\"]{\n    margin: 0;\n    height: 100%;\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    cursor: ew-resize;\n    cursor: grab;\n    overflow: hidden;\n    min-height: 1.5rem;\n}\n\ninput[type=\"range\"]:focus {\n    box-shadow: none;\n    outline: none;\n}\n\ninput[type=\"range\"]:active::-webkit-slider-thumb {\n    border-color: #3498db;\n    background-color: #3498db;\n}\n\ninput[type=\"range\"]:active::-moz-range-thumb {\n    border-color: #3498db;\n    background-color: #3498db;\n}\n\ninput[type=\"range\"]:focus::-ms-thumb {\n    border-color: #9C27B0;\n    background-color: #673AB7;\n}\n\ninput[type=\"range\"]::-moz-focus-outer { border: 0 }\n\ninput[type=\"range\"]::-webkit-slider-runnable-track {\n    background: #3498db;\n    content: '';\n    height: 2px;\n    pointer-events: none;\n}\n\ninput[type=\"range\"]::-webkit-slider-thumb {\n    width: 14px;\n    height: 14px;\n    -webkit-appearance: none;\n    appearance: none;\n    background: #fff;\n    border-radius: 50px;\n    margin-top: -6px;\n    border: 1px solid rgba(0, 0, 0, 0.15);\n    transition: .3s border-color, .3s background-color;\n}\n\ninput[type=\"range\"]::-moz-range-track {\n    width: 240px;\n    height: 2px;\n    background: rgba(0, 50, 126, 0.12);\n}\n\ninput[type=\"range\"]::-moz-range-thumb {\n    width: 14px;\n    height: 14px;\n    background: #fff;\n    border-radius: 50px;\n    border: 1px solid rgba(0, 30, 75, 0.12);\n    position: relative;\n    transition: .3s border-color, .3s background-color;\n}\n\ninput[type=\"range\"]::-moz-range-progress {\n    height: 2px;\n    background: #467fcf;\n    border: 0;\n    margin-top: 0;\n}\n\ntextarea{\n    display: block;\n    overflow: auto;\n    resize: vertical;\n}\n\nprogress{\n    overflow: auto;\n    border-radius: 50px;\n}\n\nprogress::-webkit-progress-bar{\n    background-color: #eee;\n}\n\n/* - 表单模组 */\n\n/* -- 单选多选框 */\ninput[type=\"checkbox\"], input[type=\"radio\"]{\n    float: left;\n    width: 1.5em;\n    height: 1.5em;\n    cursor: pointer;\n    position: relative;\n    margin: 0 .5em 0 0;\n    -moz-appearance: none;\n    -webkit-appearance: none;\n}\n\ninput[type=\"checkbox\"]:before, input[type=\"radio\"]:before{\n    content: '';\n    width: 100%;\n    height: 100%;\n    display: block;\n    border-radius: .2em;\n    box-shadow: 0 0 0 1px #ccc inset;\n    transition: background 0.3s, box-shadow 0.3s;\n}\n\ninput[type=\"checkbox\"]:after{\n    top: 10%;\n    left: 10%;\n    width: 30%;\n    height: 60%;\n    content: '';\n    position: absolute;\n    transition: transform .3s;\n    transform-origin: 100% 100%;\n    border-right: .15em solid #fff;\n    border-bottom: .15em solid #fff;\n    transform: rotate(45deg) scale(0);\n}\n\ninput[type=\"radio\"], input[type=\"radio\"]:before{ border-radius: 100% }\ninput[type=\"checkbox\"], input[type=\"checkbox\"]:before{ border-radius: .2em }\n\ninput[type=\"radio\"]:checked:before{\n    background: #3498db;\n    border: 2px solid #3498db;\n    box-shadow: 0 0 0 .2em #fff inset;\n}\n\ninput[type=\"checkbox\"]:checked:before{\n    box-shadow: none;\n    background: #3498db;\n}\n\ninput[type=\"checkbox\"]:checked:after{\n    transform: rotate(45deg) scale(1);\n}\n\n/* -- 开关按钮 */\ninput[type=\"checkbox\"].switch{\n    width: 4em;\n    height: 2em;\n    float: none;\n    cursor: pointer;\n    background: #eee;\n    position: relative;\n    border-radius: 50px;\n    border: 1px solid #ddd;\n    box-sizing: content-box;\n    transition: border .3s, background .3s;\n}\n\ninput[type=\"checkbox\"].switch:before{\n    margin: 0;\n    border: 0;\n    width: 2em;\n    height: 2em;\n    content: '';\n    display: block;\n    box-shadow: none;\n    background: #fff;\n    position: absolute;\n    border-radius: 100%;\n    transition: transform 0.3s;\n}\n\ninput[type=\"checkbox\"].switch:after{ content: normal }\n\ninput[type=\"checkbox\"].switch:checked{\n    box-shadow: none;\n    background: #3498db;\n    border-color: #3498db;\n}\n\ninput.switch:checked:before{\n    background: #fff;\n    transform: translateX(2em);\n}\n\n/* - 表单小组 */\nfieldset{\n    border: none;\n    margin-bottom: 2em;\n}\n\nfieldset > *{ margin-bottom: 1em }\nfieldset:last-child{ margin-bottom: 0 }\n\nfieldset legend{ margin: 0 0 1em }\n\nfieldset input:not([type=\"checkbox\"]):not([type=\"radio\"]), fieldset select, fieldset textarea{ width: 100% }\n\n/* -- 控件模块 */\nfieldset label{\n    display: block;\n    user-select: none;\n    margin-bottom: 1em;\n}\n\n/* -- 控件标题 */\nfieldset label span:first-child{\n    font-size: .85em;\n    white-space: nowrap;\n    margin-bottom: .5rem;\n    display: inline-block;\n    color: rgba(0, 0, 0, .6);\n}\n\nfieldset label.required span:after{\n    color: red;\n    content: \"*\";\n    margin-left: .25em;\n}\n\n/* -- 单行组合 */\nfieldset .has-group{ display: flex }\n\nfieldset .has-group input:not([type=\"checkbox\"]):not([type=\"radio\"]){\n    width: auto;\n    flex: 1 1 auto;\n    border-right: none;\n}\n\nfieldset .has-group .btn{\n    border-top-left-radius: 0;\n    border-bottom-left-radius: 0;\n}\n\n/* -- 单行表单 */\nform .inline label{\n    display: inline-block;\n    vertical-align: bottom;\n    margin: 0 .75em .75em 0;\n}\n\n/* - 表格 */\n.ks-table{\n    width: 100%;\n    overflow-x: auto;\n    overflow-y: hidden;\n    border-radius: 5px;\n}\n\ntable{\n    border: 0;\n    width: 100%;\n    max-width: 100%;\n    border-collapse: collapse;\n}\n\ntable th, table td{\n    padding: .75em;\n    text-align: left;\n    vertical-align: top;\n}\n\ntable > thead > tr{ border-bottom: 2px solid #ccc }\ntable > tbody > tr{ border-bottom: 1px solid #ddd }\ntable > tbody > tr:last-child{ border-bottom: 0 }\n\ntable > tbody > tr{\n    transition: background .3s;\n}\n\ntable > tbody > tr:hover{\n    background: #eee;\n}\n\ntable > thead > tr > th, table > tbody > tr > th, table > tfoot > tr > th,\ntable > thead > tr > td, table > tbody > tr > td, table > tfoot > tr > td{\n    white-space: nowrap;\n}\n\n/* - 蓝色风格 */\ntable.fill{\n    border: 1px solid transparent;\n}\n\ntable.fill > thead{\n    background: #3498db;\n    border-left: 1px solid #3498db;\n    border-right: 1px solid #3498db;\n}\ntable.fill > thead > tr{\n    border-bottom: none;\n}\ntable.fill > thead > tr > th, table.fill > thead > tr > td{\n    color: #fff;\n}\n\ntable.fill > tbody{\n    border-left: 1px solid #ddd;\n    border-right: 1px solid #ddd;\n    border-bottom: 1px solid #ddd;\n}\ntable.fill > tbody > tr:nth-child(even) > th, table.fill > tbody > tr:nth-child(even){\n    background: #f7f7f7;\n}\n\n/* - 嵌入式网页 */\niframe{\n    border: none;\n}\n\n/* - 栅格系统 */\n.row{\n    display: flex;\n    flex-wrap: wrap;\n    margin: 0 -1em -2em;\n}\n\n.row.s{ margin: 0 -.5em -1em }\n.row.m{ margin: 0 -1.25em -2.5em }\n.row.l{ margin: 0 -1.5em -3em }\n.row.full{ margin: 0 }\n.row.clear{ margin-bottom: 0 }\n\n.row.scrollable{\n    overflow: auto;\n    margin-left: 0;\n    margin-right: 0;\n    flex-wrap: nowrap;\n}\n\n/* -- 对齐方式 */\n.row.right{ justify-content: flex-end; }\n.row.center{ justify-content: center; }\n.row.around{ justify-content: space-around; }\n.row.between{ justify-content: space-between; }\n\n/* -- 网格间距 */\n.row [class*=\"col-\"]{\n    flex: 0 0 100%;\n    padding: 0 1em;\n    max-width: 100%;\n    margin-bottom: 2em;\n}\n\n.row.s [class*=\"col-\"]{\n    padding: 0 .5em;\n    margin-bottom: 1em;\n}\n.row.m [class*=\"col-\"]{\n    padding: 0 1.25em;\n    margin-bottom: 2.5em;\n}\n.row.l [class*=\"col-\"]{\n    padding: 0 1.5em;\n    margin-bottom: 3em;\n}\n.row.full [class*=\"col-\"]{\n    padding: